import { Link } from 'react-router-dom'
import {useState} from 'react'
import Menu from './Menu'
import dataCinquieme from './data/dataCinquieme'

import './CSS/Sixieme.css'

const Cinquieme = ()=>{
	const [coursTD,setCoursTD]=useState(false)
	return <>
			<Menu setTDCours={setCoursTD} />
	<div id="sixieme" >
		{coursTD == "Cours" && dataCinquieme.cours.map((el,index)=>(<div key={"cours"+index}><a href={el.pdf}>{el.name}</a><br /></div>))}
		{coursTD == "TD" && dataCinquieme.TD.map((el,index)=>(<div key={"TD"+index} ><a href={el.pdf}>{el.name}</a><br /></div>))}
		</div>
		</>
}

export default Cinquieme;